import React, { useContext } from "react";

function WorkerIcons(props) {
  let array = props.Props;

  return (
    <div className="buttons-frame ">
      {array.map((object) => {
        if (object.jca_Worker.jca_picture === null || undefined) {
          return (
            <img
              className="profilePic"
              src={require("../../Assets/noIcon.png")}
              alt=""
            />
          );
        } else {
          return (
            <img
              className="profilePic"
              src={"data:image/png;base64," + object.jca_Worker.jca_picture}
              alt=""
            />
          );
        }
      })}
    </div>
  );
}

export default WorkerIcons;
