import "../../MainStyle/mainStyle.css";
import Header from "../../components/Header";
import React, { useState, useContext, useEffect } from "react";
import Invoice from "../../components/invoiceDetails";
import { TokenContext, TypeContext } from "../../components/TokenContext";
import APICall from "../../components/APICall";
import PageLoading from "../../components/pageLoading";

function InvoicePage(params) {
  let [APIData, setData] = useState();
  const context = useContext(TokenContext);
  const typeContext = useContext(TypeContext);
  let [Loading, setLoading] = useState();
  let request = "invoice";
  if (typeContext === "worker") {
    /* Details FOR the worker ABOUT the client*/
    request = "invoice";
  } else {
    /* Details FOR the client ABOUT the worker(S)*/
    request = "invoice";
  }

  useEffect(() => {
    if (context === null) {
    } else {
      const API =
        "https://prod-25.uksouth.logic.azure.com:443/workflows/03342b30fbd141e0b96228caf72741c0/triggers/manual/paths/invoke?api-version=2016-06-01&sp=%2Ftriggers%2Fmanual%2Frun&sv=1.0&sig=p1WHKr_tpxB7Yw8yPY2FpmtDumQFzsYO0utfaX0ZNws&request=" +
        request +
        "&token=";
      setData(APICall(API, APIData, setData, context, setLoading));
    }
  }, [context]);

  return APIData ? (
    <div className="screen-view">
      <Header jobList="Invoice Approval" />
      <Invoice params={APIData} />
    </div>
  ) : (
    <PageLoading />
  );
}

export default InvoicePage;
