import "../../MainStyle/mainStyle.css";
import React, { useState } from "react";

import SuccessfulSubmission from "../SuccessfullSubmission";

function InvoiceGrid(params) {
  const param = params.params;
  var dateOptions = {
    year: "numeric",
    month: "numeric",
    day: "numeric"
  };
  var endDate = new Date(param["jca_invoiceto"]);
  var startDate = new Date(param["jca_invoicefrom"]);
  console.log(typeof startDate);
  const lineItems = param.jca_jca_item_Invoice_jca_invoice;
  const referalLineItems = lineItems.filter(function (item) {
    return item.jca_referral === true;
  });
  const workLineItems = lineItems.filter(function (item) {
    return item.jca_referral === false;
  });
  if (referalLineItems.length > 0) {
    return (
      <div className="padding">
        <h2 className="action-title purple">
          Your invoice for the period of:
          <br /> {startDate.toLocaleDateString("en-GB", dateOptions)} to{" "}
          {endDate.toLocaleDateString("en-GB", dateOptions)}
          <br />
          Total:{" "}
          {param["jca_itemtotal@OData.Community.Display.V1.FormattedValue"]}
          <br />
          Total inc. VAT:{" "}
          {param["jca_total@OData.Community.Display.V1.FormattedValue"]}
        </h2>
        <div className="card-frame ">
          <h3 className="purple">Work Done</h3>
          <table className="simple-table">
            <tbody>
              <tr className="header-row">
                <td className="cell div_body cell-header ">Address</td>
                <td className="cell cell-middle div_body cell-header">Hours</td>
                <td className="cell right-align div_body cell-header ">
                  Amount
                </td>
              </tr>
              {workLineItems.map((item) => {
                return (
                  <tr className="col3" key={item.jca_itemid}>
                    <td className="cell  div_body">{item.jca_name}</td>
                    <td className="cell cell-middle div_body">
                      {item.jca_quantity}
                    </td>
                    <td className="cell right-align div_body">
                      {
                        item[
                          "jca_total@OData.Community.Display.V1.FormattedValue"
                        ]
                      }
                    </td>
                  </tr>
                );
              })}
            </tbody>
          </table>
        </div>
        <div className="card-frame">
          <h3 className="purple">Referrals</h3>
          <table className="simple-table">
            <tbody>
              <tr className="header-row">
                <td className="cell  div_body cell-header ">Address</td>
                <td className="cell cell-middle div_body cell-header">Hours</td>
                <td className="cell right-align div_body cell-header">
                  Amount
                </td>
              </tr>
              {referalLineItems.map((item) => {
                return (
                  <tr className="col3" key={item.jca_itemid}>
                    <td className="cell  div_body">{item.jca_name}</td>
                    <td className="cell cell-middle div_body">
                      {item.jca_quantity}
                    </td>
                    <td className="cell right-align div_body">
                      {
                        item[
                          "jca_total@OData.Community.Display.V1.FormattedValue"
                        ]
                      }
                    </td>
                  </tr>
                );
              })}
            </tbody>
          </table>
        </div>
      </div>
    );
  } else {
    return (
      <div className="padding">
        <h2 className="action-title purple">
          Your invoice for the period of:
          <br /> {startDate.toLocaleDateString("en-GB", dateOptions)} to{" "}
          {endDate.toLocaleDateString("en-GB", dateOptions)}
          <br />
          Total:{" "}
          {param["jca_itemtotal@OData.Community.Display.V1.FormattedValue"]}
          <br />
          Total inc. VAT:{" "}
          {param["jca_total@OData.Community.Display.V1.FormattedValue"]}
        </h2>
        <div className="card-frame ">
          <h3 className="purple">Work Done</h3>
          <table className="simple-table">
            <tbody>
              <tr className="header-row">
                <td className="cell  div_body cell-header">Address</td>
                <td className="cell cell-middle div_body cell-header">Hours</td>
                <td className="cell right-align div_body cell-header">
                  Amount
                </td>
              </tr>
              {workLineItems.map((item) => {
                return (
                  <tr className="col3" key={item.jca_itemid}>
                    <td className="cell  div_body">{item.jca_name}</td>
                    <td className="cell cell-middle div_body">
                      {item.jca_quantity}
                    </td>
                    <td className="cell right-align div_body">
                      {
                        item[
                          "jca_total@OData.Community.Display.V1.FormattedValue"
                        ]
                      }
                    </td>
                  </tr>
                );
              })}
            </tbody>
          </table>
        </div>
      </div>
    );
  }
}

export default InvoiceGrid;
