import React, { useContext, useEffect, useState } from "react";
import "../ContactFrame";
import "../../MainStyle/mainStyle.css";
import ServicesFrame from "../ServicesFrame";
import SuccessfulSubmission from "../SuccessfullSubmission";
import APICall from "../../components/APICall";
import { TokenContext } from "../TokenContext";

function JobOfferDetails(params) {
  let [approve, setApprove] = useState();
  let [APIData, setData] = useState();
  const context = useContext(TokenContext);
  let [Loading, setLoading] = useState();
  console.log(params.props);

  const stars = require("../../Assets/starsFilled.png");

  useEffect(() => {
    if (context === null) {
    } else {
    }
  }, [context]);

  function approvalButton(boolean) {
    setApprove(boolean);
    console.log(boolean);
    const API =
      "https://prod-01.uksouth.logic.azure.com:443/workflows/b3eba0401b224ef2b2fdb8ea9ee38708/triggers/manual/paths/invoke?api-version=2016-06-01&sp=%2Ftriggers%2Fmanual%2Frun&sv=1.0&sig=atPdp1DsU1XAQDqdbxycpMCWICMOB9wpPyKCcPyf38g&approve=" +
      boolean +
      "&token=";
    setData(APICall(API, APIData, setData, context, setLoading));
    console.log(API);
  }

  //total expected hours
  let totalHours = params.props.jca_Job.jca_Address.jca_jca_service_Address_jca_address.reduce(
    (total, currentItem) => (total = total + currentItem.jca_expectedhours),
    0
  );

  switch (approve) {
    case "true":
      //Job Accepted
      return (
        <SuccessfulSubmission
          text={"Job accepted. Further details will be sent to you shortly"}
        />
      );
    case "false":
      //Job Rejected
      return (
        <SuccessfulSubmission
          text={"Job rejected. Thank you for your response"}
        />
      );
    default:
      if (
        params.props[
          "jca_offerstatus@OData.Community.Display.V1.FormattedValue"
        ] !== "Sent"
      ) {
        //Job Taken
        return (
          <div className="client-details">
            <div className="card-frame long submit-frame">
              <h2 className="action-title purple">
                Unfortunately it seems this job offer has timed out and is being
                presented to another worker.
                {<br />}
                We will keep you updated on future job offers.
              </h2>
            </div>
          </div>
        );
      } else {
        //Job Details
        return (
          <div className="client-details">
            <div className="details-frame">
              <h2 className="action-subtitle">Location</h2>
              <a
                href={
                  "https://maps.google.fr/maps?q=" + params.props.jca_postcode
                }
                target="_blank"
              >
                <p className="div_body">{params.props.jca_postcode} </p>
              </a>
            </div>

            <div className="details-frame">
              <h2 className="action-subtitle">Preferences</h2>

              <p className="div_body">
                On-Site Storage:{" "}
                {
                  params.props.jca_Job.jca_Address[
                    "jca_onsitestorage@OData.Community.Display.V1.FormattedValue"
                  ]
                }{" "}
              </p>
              <p className="div_body">
                Cleaning Products Supplied:{" "}
                {
                  params.props.jca_Job.jca_Address[
                    "jca_resourcessupplied@OData.Community.Display.V1.FormattedValue"
                  ]
                }
              </p>
              <p className="div_body">
                Eco-Cleaning:{" "}
                {
                  params.props.jca_Job.jca_Address[
                    "jca_ecocleaning@OData.Community.Display.V1.FormattedValue"
                  ]
                }
              </p>
            </div>
            <div className="details-frame">
              <h2 className="action-subtitle">Services</h2>
              <ul>
                {params.props.jca_Job.jca_Address.jca_jca_service_Address_jca_address.map(
                  (item) => {
                    return (
                      <li className="div_body list">
                        <img src={stars} className="star" alt="" />
                        {item.jca_name}
                      </li>
                    );
                  }
                )}
              </ul>
              <p className="div_body">
                Total expected hours:
                {" " + params.props.jca_hours}
              </p>
            </div>
            <div className="buttons-frame">
              <div
                className="button green-fill"
                onClick={() => approvalButton("true")}
              >
                Accept
              </div>
              <div
                className="button red-fill"
                onClick={() => approvalButton("false")}
              >
                Reject
              </div>
            </div>
          </div>
        );
      }
  }
}
export default JobOfferDetails;
