import React, { useState, useContext } from "react";
import "../../MainStyle/mainStyle.css";
import { TokenContext } from "../../components/TokenContext";
import APICall from "../../components/APICall";

import "react-phone-input-2/lib/bootstrap.css";
import SendLinkButton from "../../components/SendLinkButton";

function Home() {
  const [num, setnum] = useState();

  const phone = "" + num;
  const newPhone = "44" + phone.slice(1);
  const API =
    "https://prod-07.uksouth.logic.azure.com:443/workflows/7e16e9ef177e4c1496e6f845f6604594/triggers/manual/paths/invoke?api-version=2016-06-01&sp=%2Ftriggers%2Fmanual%2Frun&sv=1.0&sig=0a_-DYEmcJEaO6w0PhdZcAMkZv-JnRRo_vvXFFd-nkw&request=newlink&number=" +
    newPhone;
  let [APIData, setData] = useState();

  let [Loading, setLoading] = useState();

  function PingAPI() {
    setData(APICall(API, APIData, setData, "", setLoading));
  }

  switch (JSON.stringify(APIData)) {
    case JSON.stringify({ Message: "Failed" }):
      return (
        <div className="home-screen">
          <img
            src="https://encrypted-tbn1.gstatic.com/images?q=tbn:ANd9GcR6250fKX3xPd6ujGyww_E0XZO6LqNhyE13yfT_bGq_M_tHDKcy"
            alt=""
          />
          <h2 className="action-title white">myclean.expert</h2>
          <p className="div_body white">
            Message failed to send, please make sure your phone number is
            correct
          </p>
          <input
            className="text-input"
            placeholder="Phone  Number"
            type="tel"
            autoComplete="tel"
            maxLength="11"
            value={num}
            onChange={(e) => setnum(e.target.value)}
          />

          <SendLinkButton PingAPI={PingAPI} Loading={Loading} />
        </div>
      );
    case JSON.stringify({ Message: "Success" }):
      return (
        <div className="home-screen">
          <img
            src="https://encrypted-tbn1.gstatic.com/images?q=tbn:ANd9GcR6250fKX3xPd6ujGyww_E0XZO6LqNhyE13yfT_bGq_M_tHDKcy"
            alt="MyClean.Expert icon"
          />
          <h2 className="action-title white">myclean.expert</h2>
          <p className="div_body white">Message sent sucessfully</p>
          <input
            className="text-input"
            placeholder="Phone  Number"
            type="tel"
            autoComplete="tel"
            maxLength="11"
            value={num}
            onChange={(e) => setnum(e.target.value)}
          />

          <SendLinkButton PingAPI={PingAPI} Loading={Loading} />
        </div>
      );
    default:
      return (
        <div className="home-screen">
          <img
            src="https://encrypted-tbn1.gstatic.com/images?q=tbn:ANd9GcR6250fKX3xPd6ujGyww_E0XZO6LqNhyE13yfT_bGq_M_tHDKcy"
            alt=""
          />
          <h2 className="action-title white">myclean.expert</h2>

          <input
            className="text-input"
            placeholder="Phone  Number"
            type="tel"
            autoComplete="tel"
            maxLength="11"
            value={num}
            onChange={(e) => setnum(e.target.value)}
          />

          <SendLinkButton PingAPI={PingAPI} Loading={Loading} />
        </div>
      );
  }
}

export default Home;
