import React, { useState, useContext } from "react";
import "../../MainStyle/mainStyle.css";
import { TokenContext } from "../../components/TokenContext";
import APICall from "../../components/APICall";
import CustomTextArea from "../TextArea";
import FeedbackDetails from "../FeedbackDetails";
import TickIcon from "../tick";
import SuccessfulSubmission from "../SuccessfullSubmission";

function JobFeedback(params) {
  let [Loading, setLoading] = useState();
  const [page, setpage] = useState("Feedback");
  const [text, settext] = useState();
  const [num, setnum] = useState(params.params.expectedHours);
  const [checkedOne, setCheckedOne] = useState(false);
  const [rating, setRating] = useState(0);
  let [APIData, setData] = useState();
  const context = useContext(TokenContext);
  console.log(params);
  function CallbackRequest() {
    const API =
      "https://prod-24.uksouth.logic.azure.com:443/workflows/7b878a05b50c4bf899b49a88add35ae1/triggers/manual/paths/invoke?api-version=2016-06-01&sp=%2Ftriggers%2Fmanual%2Frun&sv=1.0&sig=Po4tfZGI9aN7wJNCDvpJpBCw7gLF1wv_weK7BgLixxk&request=" +
      "callbackrequest" +
      "&token=";
    APICall(API, APIData, setData, context, setLoading);
    setpage("Issue Reported");
  }

  const phoneIcon = require("../../Assets/phone.png");

  switch (page) {
    case "Issue":
      return (
        <div className="client-details">
          <div className="card-frame div_body half-height submit-frame">
            <TickIcon />
            <h2 className="action-title green">Submitted Successfully</h2>
          </div>
          <div className="card-frame div_body half-height submit-frame">
            <h3 className="action-subtitle ">
              We’re sorry that our service was below our usual high standard.
              Please click below if you wish to arrange a time to speak with us
              about how we can improve.{" "}
            </h3>
          </div>
          <div className="button" onClick={() => CallbackRequest()}>
            Request Callback{" "}
            <img className="button-icon" src={phoneIcon} alt="phone" />
          </div>
        </div>
      );

    case "Issue Reported":
      return (
        <div className="client-details">
          <div className="card-frame div_body half-height submit-frame">
            <TickIcon />
            <h2 className="action-title green">
              Issue Reported and Calback Requested
            </h2>
          </div>
          <div className="card-frame div_body half-height submit-frame">
            <h3 className="action-subtitle ">
              Our team will be in touch with you at the earliest opportunity.{" "}
            </h3>
          </div>
        </div>
      );

    case "Submit":
      return <SuccessfulSubmission text={"Submitted Successfully"} />;
    default:
      return (
        <div className="client-details">
          <h3 className="purple left-align">How did it go?</h3>
          <CustomTextArea
            params={{
              placeholder: "Tell us how it went...",
              state: text,
              setstate: settext
            }}
          />
          <div className="card-frame div_body ">
            <h3 className="purple left-align">Client</h3>
            <p className="left-align">{params.params.client}</p>
          </div>
          <FeedbackDetails
            param={params.params}
            rating={rating}
            setRating={setRating}
            checkedOne={checkedOne}
            setCheckedOne={setCheckedOne}
            num={num}
            setnum={setnum}
            setpage={setpage}
            page={page}
            text={text}
          />
        </div>
      );
  }
}
export default JobFeedback;
