import React, { useState, useEffect, useContext } from "react";
import Header from "../../components/Header";
import ClientDetailsScreen from "../../components/ClientDetailsOneScreen2";
import "./VisitDetailsClient.css";
import { useLocation } from "react-router-dom";
import { TokenContext, TypeContext } from "../../components/TokenContext";
import APICall from "../../components/APICall";
import PageLoading from "../../components/pageLoading";

function VisitDetailsClient(props) {
  const { headerProps } = props;
  const location = useLocation();
  const id = location.state;
  let [APIData, setData] = useState();
  const context = useContext(TokenContext);
  const typeContext = useContext(TypeContext);
  let [Loading, setLoading] = useState();

  let request = "";
  let NewAPI = "";

  if (typeContext === "worker") {
    /* Details FOR the worker ABOUT the client*/
    request = "clientdetails";
    NewAPI =
      "https://prod-02.uksouth.logic.azure.com:443/workflows/b76b9c4ea675449f846d23d2325c9dfc/triggers/manual/paths/invoke?api-version=2016-06-01&sp=%2Ftriggers%2Fmanual%2Frun&sv=1.0&sig=eeZFSPS-0sWr5czFKDDG-Gf5zJv0uaQKrqxiqSDSk7I&request=";
  } else {
    /* Details FOR the client ABOUT the worker(S)*/
    request = "workerdetails";
    NewAPI =
      "https://prod-16.uksouth.logic.azure.com:443/workflows/bae3654453184e438074675b9dd23e2e/triggers/manual/paths/invoke?api-version=2016-06-01&sp=%2Ftriggers%2Fmanual%2Frun&sv=1.0&sig=mL17f26tZI5DVzJd8DGa_ZGAiTPUg7RFgDwhDWyZIAg&request=";
  }

  let recordid = "";
  if (id === undefined) {
    recordid = null;
  } else {
    recordid = id.id;
  }

  useEffect(() => {
    if (context === null) {
    } else {
      const API = NewAPI + request + "&recordid=" + recordid + "&token=";
      setData(APICall(API, APIData, setData, context, setLoading));
    }
  }, [context]);

  if (id === undefined) {
    return APIData ? (
      <div>
        <Header jobList={"Visit Details"} />
        <div className="job_list">
          <ClientDetailsScreen props={APIData} />
        </div>
      </div>
    ) : (
      <PageLoading />
    );
  } else {
    return APIData ? (
      <div>
        <Header
          jobList={
            "Visit Details - " +
            (APIData[
              "_jca_job_value@OData.Community.Display.V1.FormattedValue"
            ] || APIData.jca_name)
          }
        />
        <div className="job_list">
          <ClientDetailsScreen props={APIData} />
        </div>
      </div>
    ) : (
      <PageLoading />
    );
  }
}

export default VisitDetailsClient;
