import "../../MainStyle/mainStyle.css";
import React, { useState } from "react";

function QuoteGrid(params) {
  return (
    <div className="padding">
      <h2 className="action-title purple">New Quote</h2>
      <div className="card-frame ">
        <table className="simple-table">
          <tbody>
            <tr className="header-row">
              <td className="cell div_body cell-header ">Address</td>
              <td className="cell cell-middle div_body cell-header">Service</td>
              <td className="cell cell-middle div_body cell-header">Hours</td>
              <td className="cell right-align div_body cell-header ">Amount</td>
            </tr>

            {params.params.jca_jca_item_Quote_jca_quote.map((item) => {
              return (
                <tr className="col3" key={item.jca_itemid}>
                  <td className="cell  div_body">{item.jca_addressline1}</td>
                  <td className="cell  div_body">{item.jca_name}</td>
                  <td className="cell cell-middle div_body">
                    {item.jca_quantity}
                  </td>
                  <td className="cell right-align div_body">
                    {
                      item[
                        "jca_total@OData.Community.Display.V1.FormattedValue"
                      ]
                    }
                  </td>
                </tr>
              );
            })}
            <tr className="col4">
              <td className="cell div_body"></td>
              <td className="cell div_body"></td>
              <td className="cell div_body">
                VAT @ {params.params.jca_vatratepercentage}%:
              </td>
              <td className="cell right-align div_body">
                {
                  params.params[
                    "jca_vattotal_base@OData.Community.Display.V1.FormattedValue"
                  ]
                }
              </td>
            </tr>
            <tr className="col3">
              <td className="cell div_body"></td>
              <td className="cell div_body"></td>
              <td className="cell div_body">Total:</td>
              <td className="cell right-align div_body">
                {
                  params.params[
                    "jca_total_base@OData.Community.Display.V1.FormattedValue"
                  ]
                }
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  );
}

export default QuoteGrid;
