import React, { useState, useContext } from "react";
import "../ContactFrame";
import StarRating from "../StarRating";

import { TokenContext, TypeContext } from "../../components/TokenContext";
import APICall from "../../components/APICall";

function FeedbackDetails(param) {
  let [APIData, setData] = useState();
  const context = useContext(TokenContext);
  const UserType = useContext(TypeContext);
  let [Loading, setLoading] = useState();

  const WorkerSubmit = (params) => {
    const API =
      "https://prod-03.uksouth.logic.azure.com/workflows/2434f355c8494068ba9f22a687ca36b0/triggers/manual/paths/invoke?api-version=2016-06-01&sp=%2Ftriggers%2Fmanual%2Frun&sv=1.0&sig=3ibNJOQRjNukycFVMG8XYA3-zuFjsSAHybo7EiBC1SY&request=" +
      "sendworkerfeedback" +
      "&feedback=" +
      param.text +
      "&actualhours=" +
      param.num +
      "&seriousissue=" +
      param.checkedOne +
      "&token=";
    APICall(API, APIData, setData, context, setLoading);

    if (param.checkedOne === true) {
      param.setpage("Issue Reported");
    } else {
      param.setpage("Submit");
    }
  };

  const ClientSubmit = (params) => {
    const API =
      "https://prod-06.uksouth.logic.azure.com:443/workflows/a2f45d1649df426da9a9a8f70174fb1c/triggers/manual/paths/invoke?api-version=2016-06-01&sp=%2Ftriggers%2Fmanual%2Frun&sv=1.0&sig=D7tIKq-VWmOdr-XWk2ZBj7fpc86FioELzts087By_zw&request=" +
      "sendclientfeedback" +
      "&feedback=" +
      param.text +
      "&score=" +
      param.rating +
      "&token=";
    APICall(API, APIData, setData, context, setLoading);

    if (param.rating < 4) {
      param.setpage("Issue");
    } else {
      param.setpage("Submit");
    }
  };

  if (UserType === "worker") {
    return (
      <div className="padding">
        <div className="div_body center">
          The time spent on this job was{" "}
          <input
            className="text-input-small "
            type="number"
            defaultValue={param.num}
            onChange={(e) => param.setnum(e.target.value)}
          />{" "}
          hours.
        </div>
        <br />
        <div className="div_body center">
          <input
            type="checkbox"
            label="Serious Issue"
            defaultValue={param.checkedOne}
            onChange={() => param.setCheckedOne(!param.checkedOne)}
          />
          Report Serious Issue
        </div>
        <br />
        <div className="center">
          <div className="button InvoiceButton" onClick={() => WorkerSubmit()}>
            Submit
          </div>
        </div>
      </div>
    );
  } else {
    return (
      <div className="padding">
        <h3 className="purple">Rate our service:</h3>
        <StarRating setRating={param.setRating} rating={param.rating} />
        <br />
        <div className="center">
          <div className="button green-fill " onClick={() => ClientSubmit()}>
            Submit
          </div>
        </div>
      </div>
    );
  }
}

export default FeedbackDetails;
