import React, { useState, useContext } from "react";
import ContactUs from "../ContactUs";
import ClientDetails2 from "../ClientDetails2";
import WorkerRearrangeButton from "../WorkerDelayButton";
import "./ClientDetailsOneScreen2.css";
import SuccessfulSubmission from "../SuccessfullSubmission";
import { TypeContext } from "../../components/TokenContext";
import WorkerIcons from "../WorkerIcons";

function ClientDetailsScreen(props) {
  const [Screen, setScreen] = useState(1);

  const UserType = useContext(TypeContext);

  switch (UserType === "worker") {
    case true:
      return (
        <div>
          <ClientDetails2
            contactFrame1Props={props.props.jca_Job.jca_name}
            jca_Address={props.props.jca_Job.jca_Address}
            contactFrame2Props={props.props.jca_Job.jca_Address}
            servicesFrameProps={
              props.props.jca_Job.jca_Address
                .jca_jca_service_Address_jca_address
            }
            totalHours={props.props.jca_totalexpectedhours}
            StartTime={
              props.props["jca_start@OData.Community.Display.V1.FormattedValue"]
            }
          />
          <WorkerRearrangeButton
            props={props.props.jca_teammemberid}
            cancelled={props.props.jca_cancel}
          ></WorkerRearrangeButton>
        </div>
      );

    default:
      switch (Screen) {
        default:
          if (props.props.jca_cancel === false) {
            return (
              <div className="client-details-one-screen">
                <WorkerIcons
                  Props={props.props.jca_jca_teammember_Job_jca_job}
                />
                <ClientDetails2
                  contactFrame1Props={
                    props.props.jca_jca_teammember_Job_jca_job
                  }
                  jca_Address={props.props.jca_Address}
                  Preferences={props.props.jca_Address}
                  servicesFrameProps={
                    props.props.jca_Address.jca_jca_service_Address_jca_address
                  }
                  totalHours={props.props.jca_totalexpectedhours}
                />
                <div className="button" onClick={() => setScreen(2)}>
                  Contact Us
                </div>
              </div>
            );
          } else {
            return (
              <div className="client-details-one-screen">
                <WorkerIcons
                  Props={props.props.jca_jca_teammember_Job_jca_job}
                />
                <ClientDetails2
                  contactFrame1Props={
                    props.props.jca_jca_teammember_Job_jca_job
                  }
                  jca_Address={props.props.jca_Address}
                  Preferences={props.props.jca_Address}
                  servicesFrameProps={
                    props.props.jca_Address.jca_jca_service_Address_jca_address
                  }
                  totalHours={props.props.jca_totalexpectedhours}
                />
                <h3 className="action-title purple">Cancelled</h3>
              </div>
            );
          }
        case 2:
          return (
            <ContactUs
              Screen={Screen}
              setScreen={setScreen}
              jobid={props.props.jca_jobid}
            />
          );
        case 3:
          return <SuccessfulSubmission text={"Submitted Successfully"} />;
      }
  }
}

export default ClientDetailsScreen;
