import React from "react";
import "../../MainStyle/mainStyle.css";

import TickIcon from "../tick";

function SuccessfulSubmission(text) {
  return (
    <div className="padding">
      <div className="card-frame long submit-frame">
        <TickIcon />
        <h2 className="action-title green">{text.text}</h2>
      </div>
    </div>
  );
}

export default SuccessfulSubmission;
