import React, { useState, useEffect, useContext } from "react";
import APICall from "../../components/APICall";
import "../../components/ContactFrame/ContactFrame.css";
import Header from "../../components/Header";
import PageLoading from "../../components/pageLoading";
import { TokenContext } from "../../components/TokenContext";
import VisitList from "../../components/VisitList";

function VisitListScreen(props) {
  let [APIData, setData] = useState();
  let [Loading, setLoading] = useState();
  const context = useContext(TokenContext);

  var date = new Date();
  var d = new Date();
  var newday = new Date(
    d.getFullYear(),
    d.getMonth(),
    d.getDate() - d.getDay() + (d.getDay() == 0 ? -6 : 1)
  );
  const [datee, setdatee] = useState(newday);

  function QuickAPI() {
    const API =
      "https://prod-28.uksouth.logic.azure.com:443/workflows/b822b5bf403f446db372d001ed2503f8/triggers/manual/paths/invoke?api-version=2016-06-01&sp=%2Ftriggers%2Fmanual%2Frun&sv=1.0&sig=goh0cnFApCYmaVOBaLu032K1CPvYLJPWTWdBz5XTmi8&request=visits" +
      "&startofweek=" +
      datee.toISOString() +
      "&token=";

    setData(APICall(API, APIData, setData, context, setLoading));
  }
  console.log(context);
  useEffect(() => {
    if (context === null) {
    } else {
      QuickAPI();
    }
  }, [context, datee]);

  return APIData ? (
    <div className="screen-view">
      <Header jobList="Services" />
      <VisitList
        APIData={APIData}
        setdatee={setdatee}
        datee={datee}
        loading={Loading}
      />
    </div>
  ) : (
    <PageLoading />
  );
}

export default VisitListScreen;
