import React, { useState, useContext } from "react";
import "../ContactFrame";
import { TokenContext } from "../../components/TokenContext";
import CustomTextArea from "../TextArea";
import APICall from "../APICall";

function ContactUs(props) {
  const [text, settext] = useState();
  let [Loading, setLoading] = useState();
  const TextBoxData = {
    placeholder: "Tell us your issue...",
    state: text,
    setstate: settext
  };

  const API =
    "https://prod-18.uksouth.logic.azure.com:443/workflows/393fa4d652274285934971201ff57b6d/triggers/manual/paths/invoke?api-version=2016-06-01&sp=%2Ftriggers%2Fmanual%2Frun&sv=1.0&sig=bSyDLVnfp0NFnlxV68YNFyfq9L10X-B3FDTISxLCg0o&request=clientcontact" +
    "&message=" +
    text +
    "&recordid=" +
    props.jobid +
    "&token=";
  let [APIData, setData] = useState();
  const context = useContext(TokenContext, setLoading);

  function APIAndChangePage() {
    APICall(API, APIData, setData, context, setLoading);
    props.setScreen(3);
  }

  return (
    <div className="padding">
      <h2 className="action-title purple">Send us a message</h2>
      <CustomTextArea params={TextBoxData} />

      <div className="button submit" onClick={() => APIAndChangePage()}>
        Submit
      </div>
    </div>
  );
}

export default ContactUs;
