import React from "react";
import "./Header.css";
import { useHistory } from "react-router-dom";

function Header(props) {
  const { jobList } = props;

  let navigate = useHistory();
  const routeChange = () => {
    let path = `/visits`;
    navigate.push({
      pathname: path
    });
  };

  return (
    <div className="header sticky">
      <img
        className="icon-frame"
        src="https://i.stack.imgur.com/D2aG7.png"
        alt="iconFrame"
        onClick={() => routeChange()}
      />
      <div className="title-label">
        <h1 className="job-list" style={{ overflow: "hidden" }}>
          {jobList}
        </h1>
      </div>
    </div>
  );
}

export default Header;
