import React, { useState, useContext, useEffect } from "react";
import "../RearrangeButton/RearrangeButton.css";
import TickIcon from "../tick";
import CustomTextArea from "../TextArea";
import { TokenContext, TypeContext } from "../../components/TokenContext";
import APICall from "../../components/APICall";

function WorkerRearrangeButton(props) {
  const [pageFooterType, incrementPageFooterType] = useState(1);
  const [time, rebookTime] = useState(0);
  const [text, settext] = useState();
  let [APIData, setData] = useState();
  let [finalButtonText, setfinalButtonText] = useState("Add 1Hrs");
  const context = useContext(TokenContext);
  const [Loading, setLoading] = useState();

  function rebookTimeAndIncrement(prop) {
    let API =
      "https://prod-12.uksouth.logic.azure.com:443/workflows/467fee0a7bb940b480f6107c7a3c2cd1/triggers/manual/paths/invoke?api-version=2016-06-01&sp=%2Ftriggers%2Fmanual%2Frun&sv=1.0&sig=auusO05OAwgeOQoN65tB7CLBezvDDX5KukNOfnUS8XY&request=workerdelay" +
      "&recordid=" +
      props.props +
      "&reason=" +
      text +
      "&delay=" +
      prop +
      "&token=";
    APICall(API, APIData, setData, context, setLoading);
    rebookTime("delayed by " + prop + " Mins");
    incrementPageFooterType(3);
  }

  function rebookAndIncrement() {
    let API =
      "https://prod-31.uksouth.logic.azure.com:443/workflows/84967ad6002b46d5bbaa15cfc494292d/triggers/manual/paths/invoke?api-version=2016-06-01&sp=%2Ftriggers%2Fmanual%2Frun&sv=1.0&sig=_qqB2X26Z7vvziUmUNNtJwLht5aoUp7Tc05sdd7aRtY&request=workerrebook" +
      "&recordid=" +
      props.props +
      "&reason=" +
      text +
      "&token=";
    APICall(API, APIData, setData, context, setLoading);
    rebookTime("forced to rebook their session");
    incrementPageFooterType(3);
  }
  function addTimeOrRebook() {
    if (time > 59) {
      rebookAndIncrement();
    } else {
      setfinalButtonText("Rebook");
      rebookTime(60);
    }
  }

  useEffect(() => {
    if (props.cancelled) {
      incrementPageFooterType(5);
    } else {
    }
  }, [props]);

  switch (pageFooterType) {
    default:
      return (
        <div className="buttons-frame">
          <div className="button">
            <div
              className="button-text"
              onClick={() => incrementPageFooterType(2)}
            >
              Running Late
            </div>
          </div>
          <div className="button">
            <div
              className="button-text"
              onClick={() => incrementPageFooterType(4)}
            >
              Rebook
            </div>
          </div>
        </div>
      );
    case 2:
      return (
        <div>
          <h3>I'll be late by...</h3>
          <CustomTextArea
            params={{
              placeholder: "Please explain the reason for the delay...",
              state: text,
              setstate: settext
            }}
          />
          <div className="buttons-frame">
            <div className="button">
              <div
                className="button-text"
                onClick={() => rebookTimeAndIncrement(time + 15)}
              >
                {time + 15} Mins
              </div>
            </div>
            <div
              className="button"
              onClick={() => rebookTimeAndIncrement(time + 30)}
            >
              <div className="button-text">{time + 30} Mins</div>
            </div>
            <div
              className="button"
              onClick={() => rebookTimeAndIncrement(time + 60)}
            >
              <div className="button-text">{(time + 60) / 60} Hrs</div>
            </div>
            <div className="button" onClick={() => addTimeOrRebook()}>
              <div className="button-text">{finalButtonText}</div>
            </div>
          </div>
        </div>
      );
    case 3:
      return (
        <div className="buttons-frame vertical">
          <h3 className="action-subtitle green">
            Thank You <br /> We will notify the client that you have been {time}
          </h3>
          <br />
          <TickIcon />
        </div>
      );
    case 4:
      return (
        <div>
          <h3>Confirmation of rebooking</h3>
          <CustomTextArea
            params={{
              placeholder:
                "Please explain the reason this visit needs to be rebooked...",
              state: text,
              setstate: settext
            }}
          />
          <div className="buttons-frame">
            <div className="button" onClick={() => rebookAndIncrement()}>
              <div className="button-text">Rebook</div>
            </div>
          </div>
        </div>
      );
    case 5:
      return <h3 className="action-title purple">Cancelled</h3>;
  }
}

export default WorkerRearrangeButton;
