import "../../MainStyle/mainStyle.css";
import React, { useState } from "react";

import InvoiceGrid from "../invoiceGrids";
import SuccessfulSubmission from "../SuccessfullSubmission";
import InvoiceButton from "../invoiceButtons";

function Invoice(param) {
  const [page, setpage] = useState("invoice");

  switch (page) {
    case "approved":
      return (
        <SuccessfulSubmission text={"Approved and Submitted Successfully"} />
      );
    case "submit":
      return <SuccessfulSubmission text={"Question Submitted Successfully"} />;
    default:
      return (
        <div className="client-details">
          {" "}
          <InvoiceGrid params={param.params} />
          <InvoiceButton
            setpage={setpage}
            page={page}
            URL={param.params.jca_paymenturl}
          />
        </div>
      );
  }
}
export default Invoice;
