import "../../MainStyle/mainStyle.css";
import React, { useState, useContext } from "react";
import { TokenContext, TypeContext } from "../../components/TokenContext";
import APICall from "../../components/APICall";
import CustomTextArea from "../TextArea";

function InvoiceButton(param) {
  const [layout, setlayout] = useState(1);
  const [text, settext] = useState();
  let [APIData, setData] = useState();
  const [Loading, setLoading] = useState();
  const context = useContext(TokenContext);
  const typeContext = useContext(TypeContext);

  function Question() {
    const API =
      "https://prod-03.uksouth.logic.azure.com:443/workflows/06d5f786526440e6b5da4481401d0213/triggers/manual/paths/invoke?api-version=2016-06-01&sp=%2Ftriggers%2Fmanual%2Frun&sv=1.0&sig=wXE2UaSibNJQ0lTzZmOp7G4Ei6yfbrhAGpPAuH__w5k&request=" +
      "invoicequery&message=" +
      text +
      "&token=";
    APICall(API, APIData, setData, context, setLoading);
    param.setpage("submit");
  }

  function Approve() {
    const API =
      "https://prod-13.uksouth.logic.azure.com:443/workflows/85904a86095b4fc0beb06796cef4f392/triggers/manual/paths/invoke?api-version=2016-06-01&sp=%2Ftriggers%2Fmanual%2Frun&sv=1.0&sig=nyQcVADEKgjLNPlEcp_NZV5ZJTSk9LrwvVINHW_uUI4&request=" +
      "invoiceapprove&token=";
    APICall(API, APIData, setData, context, setLoading);
    param.setpage("approved");
  }

  if (layout === 1) {
    if (typeContext === "worker") {
      return (
        <div className="padding">
          <div className="button" onClick={() => setlayout(2)}>
            I have a Question...{" "}
          </div>
          <h3>Or</h3>
          <div className="button green-fill" onClick={() => Approve()}>
            Approve
          </div>
        </div>
      );
    } else {
      return (
        <div className="padding">
          <div className="button" onClick={() => setlayout(2)}>
            I have a Question...{" "}
          </div>
          <h3>Or</h3>
          <a href={param.URL}>
            {" "}
            <div className="button green-fill">Pay Now</div>
          </a>
        </div>
      );
    }
  } else {
    return (
      <div className="padding">
        <h3 className="purple">Your Question...</h3>

        <CustomTextArea
          params={{
            placeholder: "Enter your question here...",
            state: text,
            setstate: settext
          }}
        />
        <div className="buttons-frame">
          <div className="button" onClick={() => Question()}>
            Submit Question
          </div>

          <div className="button green-fill" onClick={() => Approve()}>
            Approve
          </div>
        </div>
      </div>
    );
  }
}

export default InvoiceButton;
