import React, { useContext, useState } from "react";
import APICall from "../APICall";
import { TokenContext } from "../TokenContext";

function QuoteButtons(param) {
  let [APIData, setData] = useState();
  const [Loading, setLoading] = useState();
  const context = useContext(TokenContext);

  function Approve() {
    const API =
      "https://prod-13.uksouth.logic.azure.com:443/workflows/bc8d65396be14d48b4f2526d72788431/triggers/manual/paths/invoke?api-version=2016-06-01&sp=%2Ftriggers%2Fmanual%2Frun&sv=1.0&sig=4BBr7yXdsX5xuTqGYOj1PUgHlCwSeLEQ8SnE-XfKEZ8" +
      "&token=";
    APICall(API, APIData, setData, context, setLoading);
    param.setpage("accepted");
  }
  function Reject() {
    param.setpage("rejected");
  }

  return (
    <div className="buttons-frame">
      <div className="button green-fill">
        <div className="button-text" onClick={() => Approve()}>
          Accept
        </div>
      </div>
      <div className="button red-fill">
        <div className="button-text " onClick={() => Reject()}>
          Reject
        </div>
      </div>
    </div>
  );
}

export default QuoteButtons;
