import React, { useState } from "react";
import "../ContactFrame";
import "../../MainStyle/mainStyle.css";

function CustomTextArea(params) {
  return (
    <textarea
      className="card-frame div_body medium"
      placeholder={params.params.placeholder}
      value={params.params.state}
      onChange={(e) => params.params.setstate(e.target.value)}
    />
  );
}
export default CustomTextArea;
