import React, { useState } from "react";
import "../ContactFrame/ContactFrame.css";
import SimpleTable from "../SimpleTable";
import { format } from "date-fns";

function VisitList(props) {
  function thisweek() {
    var d = new Date();
    var newday = new Date(
      d.getFullYear(),
      d.getMonth(),
      d.getDate() - d.getDay() + (d.getDay() == 0 ? -6 : 1)
    );

    props.setdatee(newday);
  }

  function addtoDate() {
    var nextweek = new Date(
      props.datee.getFullYear(),
      props.datee.getMonth(),
      props.datee.getDate() +
        7 -
        props.datee.getDay() +
        (props.datee.getDay() == 0 ? -6 : 1)
    );

    props.setdatee(nextweek);
  }
  function takefromDate() {
    var lastweek = new Date(
      props.datee.getFullYear(),
      props.datee.getMonth(),
      props.datee.getDate() -
        7 -
        props.datee.getDay() +
        (props.datee.getDay() == 0 ? -6 : 1)
    );

    props.setdatee(lastweek);
  }

  let totalHours = props.APIData.reduce(
    (total, currentItem) =>
      (total = total + currentItem.jca_totalexpectedhours),
    0
  );
  var dateOptions = {
    weekday: "long",
    year: "numeric",
    month: "long",
    day: "numeric"
  };
  return (
    <div className="client-details">
      <div className="card-frame">
        <h2 className="action-subtitle">Visits</h2>
        <h2 className="action-subtitle">
          Week beginning {props.datee.toLocaleDateString("en-GB", dateOptions)}
        </h2>
        <h2 className="action-subtitle">Total hours: {totalHours}hrs</h2>
        <div className="buttons-frame">
          <div className="button" onClick={() => takefromDate()}>
            Previous Week
          </div>
          <div className="button" onClick={() => thisweek()}>
            Current Week
          </div>
          <div className="button" onClick={() => addtoDate()}>
            Next Week
          </div>
        </div>

        <SimpleTable
          TableArray={props.APIData}
          NavigateThrough={true}
          loading={props.loading}
        />
      </div>
    </div>
  );
}

export default VisitList;
