import React, { createContext, useState, useEffect } from "react";

// create context
const TokenContext = createContext();
const TypeContext = createContext();

const TokenContextProvider = ({ children }) => {
  // the value that will be given to the context
  const [token, setToken] = useState(null);
  const [Type, setType] = useState(null);

  const crypto = require("crypto");
  // fetch a user from a fake backend API
  /* Use Effect call */

  const fetchstuff = () => {
    /* getting date in right format */
    var date = new Date();
    let Month = "";
    if (date.getMonth() + 1 < 10) {
      Month = "0" + (date.getMonth() + 1);
    } else {
      Month = date.getMonth() + 1;
    }
    let Day = "";
    if (date.getDate() + 1 < 10) {
      Day = "0" + date.getDate();
    } else {
      Day = date.getDate();
    }
    var today = parseInt("" + date.getFullYear() + Month + Day, 10);

    /* finding token */
    const queryParams = new URLSearchParams(window.location.search);

    let unhashedToken = localStorage.unhashedToken;
    if (queryParams.get("token") === null) {
      unhashedToken = localStorage.unhashedToken;
    } else {
      unhashedToken = queryParams.get("token");
      localStorage.setItem("unhashedToken", unhashedToken);
    }

    const hashedToken = crypto
      .createHash("md5")
      .update(unhashedToken)
      .digest("hex");

    /* API prerequisites */
    const API =
      "https://prod-00.uksouth.logic.azure.com:443/workflows/e935b1e1f13d48249ae9058e233d3bf0/triggers/manual/paths/invoke?api-version=2016-06-01&sp=%2Ftriggers%2Fmanual%2Frun&sv=1.0&sig=BZzQyDN5rr_vBg_tgn3BJgWWMACJUILQf4HJ9pu73Ik";
    const requestBody = { token: hashedToken, date: today };
    const requestHead = { "Content-Type": "application/json" };
    /* API call */
    const requestDetails = {
      method: "POST",
      headers: requestHead,
      body: JSON.stringify(requestBody)
    };
    const fetchToken = () => {
      fetch(API, requestDetails)
        .then((response) => response.json())
        .then((data) => {
          setType(data["userType"]);
          let temp = data["accessToken"];

          setToken(crypto.createHash("md5").update(temp).digest("hex"));
        })
        .catch((err) => {
          console.log(err.message);
        });
    };

    fetchToken();
  };

  if (token === null) {
    fetchstuff();
  } else {
  }

  return (
    // the Provider gives access to the context to its children
    <TokenContext.Provider value={token}>
      <TypeContext.Provider value={Type}>{children}</TypeContext.Provider>
    </TokenContext.Provider>
  );
};

export { TokenContext, TokenContextProvider, TypeContext };
