import React, { useState, useContext, useEffect } from "react";
import "../../MainStyle/mainStyle.css";
import { TokenContext } from "../../components/TokenContext";
import APICall from "../../components/APICall";
import PageLoading from "../../components/pageLoading";
import "react-phone-input-2/lib/bootstrap.css";

import Header from "../../components/Header";
import QuoteDetails from "../../components/QuoteDetails";

function QuoteDetailsPage() {
  let [APIData, setData] = useState();
  const context = useContext(TokenContext);

  let [Loading, setLoading] = useState();

  useEffect(() => {
    if (context === null) {
    } else {
      const API =
        "https://prod-31.uksouth.logic.azure.com:443/workflows/4775817a5d614f50985a3673e6661bed/triggers/manual/paths/invoke?api-version=2016-06-01&sp=%2Ftriggers%2Fmanual%2Frun&sv=1.0&sig=IfYPH57CxER-3H25PKOlygpJ9amB1us6atPVA7IogEE&token=";
      setData(APICall(API, APIData, setData, context, setLoading));
    }
  }, [context]);

  return APIData ? (
    <div className="screen-view">
      <Header jobList="Quote Approval" />
      <QuoteDetails params={APIData} />
    </div>
  ) : (
    <PageLoading />
  );
}
export default QuoteDetailsPage;
