import React, { useState, useContext, useEffect } from "react";
import { TokenContext } from "../../components/TokenContext";
import APICall from "../../components/APICall";
import Header from "../../components/Header";
import SuccessfulSubmission from "../../components/SuccessfullSubmission";
function Verify() {
  let [APIData, setData] = useState();
  const context = useContext(TokenContext);
  let [Loading, setLoading] = useState();
  let request = "verify";

  console.log(context);

  useEffect(() => {
    if (context === null) {
    } else {
      const API =
        "https://prod-03.uksouth.logic.azure.com/workflows/2434f355c8494068ba9f22a687ca36b0/triggers/manual/paths/invoke?api-version=2016-06-01&sp=%2Ftriggers%2Fmanual%2Frun&sv=1.0&sig=3ibNJOQRjNukycFVMG8XYA3-zuFjsSAHybo7EiBC1SY&request=" +
        request +
        "&token=";
      setData(APICall(API, APIData, setData, context, setLoading));
    }
  }, [context]);

  return (
    <div className="screen-view">
      <Header jobList="Verify Phone Number" />
      <SuccessfulSubmission text={"Phone Number Successfully Verified"} />
    </div>
  );
}

export default Verify;
