import "./App.css";
import React from "react";
import { Switch, BrowserRouter as Router, Route } from "react-router-dom";
import VisitDetailsClient from "./Pages/VisitDetailsClient";
import VisitListScreen from "./Pages/visitListScreen";
import "./MainStyle/mainStyle.css";
import Home from "./Pages/HomeScreen";
import FeedbackPage from "./Pages/FeedbackPage";
import InvoicePage from "./Pages/InvoiceApproval/Index";
import { TokenContextProvider, TokenContext } from "./components/TokenContext";
import Verify from "./Pages/VerifyPhonePage/Index";
import JobOffer from "./Pages/jobOfferPage";
import QuoteDetailsPage from "./Pages/QuoteDetailsPage";

function App() {
  return TokenContext ? (
    <Router>
      <Switch>
        {/* Home */}
        <Route path="/:path(|login)">
          <Home />
        </Route>
        {/* verify Phone number */}
        <Route path="/:path(|verify)">
          <TokenContextProvider>
            <Verify />
          </TokenContextProvider>
        </Route>
        {/* Quote Approval */}
        <Route path="/:path(|quote)">
          <TokenContextProvider>
            <QuoteDetailsPage />
          </TokenContextProvider>
        </Route>
        {/* invoice Approval */}
        <Route path="/:path(|invoice)">
          <TokenContextProvider>
            <InvoicePage />
          </TokenContextProvider>
        </Route>
        {/*visit list */}
        <Route path="/:path(|visits)">
          <TokenContextProvider>
            <VisitListScreen />
          </TokenContextProvider>
        </Route>
        {/* feedback */}
        <Route path="/:path(|feedback)">
          <TokenContextProvider>
            <FeedbackPage />
          </TokenContextProvider>
        </Route>
        {/*visit details for client*/}
        <Route path="/:path(|visitdetails)">
          <TokenContextProvider>
            <VisitDetailsClient />
          </TokenContextProvider>
        </Route>
        <Route path="/:path(|joboffer)">
          <TokenContextProvider>
            <JobOffer />
          </TokenContextProvider>
        </Route>
      </Switch>
    </Router>
  ) : null;
}

export default App;
