import React from "react";
import "../../MainStyle/mainStyle.css";
import { useHistory } from "react-router-dom";

import { format } from "date-fns";

function SimpleTable(props) {
  const { TableArray, NavigateThrough } = props;

  let navigate = useHistory();
  const routeChange = (thisItem) => {
    let path = `/visitdetails`;
    navigate.push({
      pathname: path,
      state: { thisItem: thisItem, id: thisItem.jca_Job.jca_jobid }
    });
  };
  if (props.loading) {
    return (
      <div className="buttons-frame">
        <div class="circle"></div>
      </div>
    );
  } else {
    if (TableArray.length === 0) {
      return (
        <div className="buttons-frame">
          <h2 className="action-subtitle">No Visits This Week...</h2>
        </div>
      );
    } else {
      if (NavigateThrough === true) {
        return (
          <table className="simple-table">
            <tbody>
              {TableArray.map((item) => {
                return (
                  <tr
                    className="col3 Select-Row"
                    onClick={() => routeChange(item)}
                    key={item._jca_job_value}
                  >
                    <td className="cell  div_body">{item.jca_Job.jca_name}</td>
                    <td className="cell cell-middle div_body">
                      {item.jca_totalexpectedhours}hrs
                    </td>
                    <td className="cell div_body">
                      {format(new Date(item.jca_Job.jca_start), "dd/MM/yyyy")}
                    </td>
                  </tr>
                );
              })}
            </tbody>
          </table>
        );
      } else {
        return (
          <table className="simple-table">
            <tbody>
              {TableArray.map((item) => {
                return (
                  <tr className="col3" key={item._jca_job_value}>
                    <td className="cell  div_body">
                      {
                        item[
                          "_jca_job_value@OData.Community.Display.V1.FormattedValue"
                        ]
                      }
                    </td>
                    <td className="cell cell-middle div_body">
                      {item.jca_totalexpectedhours}hrs
                    </td>
                    <td className="cell div_body">
                      {format(new Date(item.jca_Job.jca_start), "dd/MM/yyyy")}
                    </td>
                  </tr>
                );
              })}
            </tbody>
          </table>
        );
      }
    }
  }
}

export default SimpleTable;
