import React, { useContext } from "react";
import "./ContactFrame.css";
import { TypeContext } from "../../components/TokenContext";

function ContactFrame(props) {
  const UserType = useContext(TypeContext);

  if (UserType === "worker") {
    return (
      <div className="details-frame ">
        <h2 className="action-subtitle">Contact</h2>
        <p className="div_body">
          {props.props.split("-")[0] || props.props.jca_fullname}
        </p>
        {/*   <p className="div_body">{props.props.telephone1 || props.props.mobilephone}</p>
        <p className="div_body">{props.props.emailaddress1}</p> */}
        <p className="div_body">{props.StartTime}</p>
      </div>
    );
  } else {
    let array = props.props;

    let arrayOfNames = array.map(
      (object) =>
        object["_jca_worker_value@OData.Community.Display.V1.FormattedValue"]
    );

    return (
      <div className="details-frame ">
        <h2 className="action-subtitle">Contacts</h2>
        <p className="div_body">{arrayOfNames.join(", ")}</p>
        <p className="div_body">{props.StartTime}</p>
      </div>
    );
  }
}

export default ContactFrame;
