import React, { useState } from "react";
import QuoteButtons from "../QuoteButtons";
import QuoteGrid from "../QuoteGrid";
import SuccessfulSubmission from "../SuccessfullSubmission";

function QuoteDetails(param) {
  const [page, setpage] = useState("quote");

  switch (page) {
    case "accepted":
      return (
        <SuccessfulSubmission
          text={"Quote accepted. Further details will be sent to you shortly!"}
        />
      );
    case "rejected":
      return (
        <SuccessfulSubmission
          text={"Quote rejected. Thank you for your response"}
        />
      );
    default:
      if (param.params.jca_accepted !== true) {
        setpage("accepted");
      }
      return (
        <div className="client-details">
          <QuoteGrid params={param.params} />
          <QuoteButtons setpage={setpage} page={page} />
        </div>
      );
  }
}
export default QuoteDetails;
