import React, { useState, useEffect, useContext } from "react";
import Header from "../../components/Header";
import { TokenContext, TypeContext } from "../../components/TokenContext";
import APICall from "../../components/APICall";
import PageLoading from "../../components/pageLoading";
import JobOfferDetails from "../../components/JobOfferDetails";

function JobOffer(props) {
  let [APIData, setData] = useState();
  const context = useContext(TokenContext);
  let [Loading, setLoading] = useState();

  let request = "joboffer"; //may change

  const API =
    "https://prod-31.uksouth.logic.azure.com:443/workflows/83582118308344f996fb36f9c1de224a/triggers/manual/paths/invoke?api-version=2016-06-01&sp=%2Ftriggers%2Fmanual%2Frun&sv=1.0&sig=b8an6AAYGAdI5AaLBHvo90L2XCNiLwcEYK_xzRCjEPI&request=" +
    request +
    "&token=";

  useEffect(() => {
    if (context === null) {
    } else {
      setData(APICall(API, APIData, setData, context, setLoading));
    }
  }, [context]);

  return APIData ? (
    <div>
      <Header jobList={"Job Offer"} />
      <div className="job_list">
        <JobOfferDetails props={APIData} />
      </div>
    </div>
  ) : (
    <PageLoading />
  );
}

export default JobOffer;
