import React, { useState, useEffect, useContext } from "react";
import "../../MainStyle/mainStyle.css";
import { TokenContext, TypeContext } from "../../components/TokenContext";
import APICall from "../../components/APICall";
import Header from "../../components/Header";
import JobFeedback from "../../components/JobFeedback";
import PageLoading from "../../components/pageLoading";

function FeedbackPage(params) {
  let [APIData, setData] = useState();
  const context = useContext(TokenContext);
  const typeContext = useContext(TypeContext);
  const [Loading, setLoading] = useState();

  let request = "";
  let newAPI = "";

  if (typeContext === "worker") {
    /* Details FOR the worker ABOUT the client*/
    request = "workerfeedback";
    newAPI =
      "https://prod-13.uksouth.logic.azure.com:443/workflows/84309dfaa9f74520865ba3229ddc219c/triggers/manual/paths/invoke?api-version=2016-06-01&sp=%2Ftriggers%2Fmanual%2Frun&sv=1.0&sig=6J7VEbVwC7tFqdcq1g6syxhkHsjsYxk_xAvkQ3Y0VVg&request=";
  } else {
    /* Details FOR the client ABOUT the worker(s)*/
    request = "clientfeedbackdata";
    newAPI =
      "https://prod-09.uksouth.logic.azure.com:443/workflows/f8ba51c0c45f42d4aef766adbc0d0ae4/triggers/manual/paths/invoke?api-version=2016-06-01&sp=%2Ftriggers%2Fmanual%2Frun&sv=1.0&sig=_xE_ODNP6G2SFW2MyolsuOoVf5ZBtfQMjyQMIzf342I&request=";
  }

  const API = newAPI + request + "&token=";

  useEffect(() => {
    if (context === null) {
    } else {
      setData(APICall(API, APIData, setData, context, setLoading));
    }
  }, [context]);

  if (typeContext === "worker") {
    return APIData ? (
      <div className="screen-view ">
        <Header jobList="Feedback" />
        <JobFeedback
          params={{
            expectedHours: APIData.jca_totalexpectedhours,
            client: APIData.jca_Job.jca_name
          }}
        />
      </div>
    ) : (
      <PageLoading />
    );
  } else {
    return APIData ? (
      <div className="screen-view ">
        <Header jobList="Feedback" />
        <JobFeedback
          params={{
            client: APIData.jca_name
          }}
        />
      </div>
    ) : (
      <PageLoading />
    );
  }
}

export default FeedbackPage;
